import { FORMS_SECURITY_USER_ENTITY_TYPE } from "../entities.js";
import type { FormsSecurityUserWorkspaceContext } from "./security-user-workspace.context.js";
import { UmbContextToken } from "@umbraco-cms/backoffice/context-api";
import type { UmbWorkspaceContext } from "@umbraco-cms/backoffice/workspace";

export const FORMS_SECURITY_USER_WORKSPACE_CONTEXT = new UmbContextToken<
  UmbWorkspaceContext,
  FormsSecurityUserWorkspaceContext
>(
  "UmbWorkspaceContext",
  undefined,
  (context): context is FormsSecurityUserWorkspaceContext =>
    context.getEntityType?.() === FORMS_SECURITY_USER_ENTITY_TYPE,
);
